/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { useStaticQuery, graphql } from 'gatsby';
import Image from 'gatsby-image';
import { ImageSharp } from 'src/models';
import { Media } from 'src/utils';
import { useColor } from 'hooks/useColor';
import { PaletteType } from '@material-ui/core';
import { useMemo } from 'react';

interface QueryResult {
  logoImage: ImageSharp;
  logoWhiteImage: ImageSharp;
}

interface Props {
  size?: number;
  color?: PaletteType;
}
const LubyconLogo = ({ size = 30, color = 'light', ...rest }: Props) => {
  const { logoImage, logoWhiteImage }: QueryResult = useStaticQuery(graphql`
    query LogoQuery {
      logoImage: file(absolutePath: { regex: "/logo.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
      logoWhiteImage: file(absolutePath: { regex: "/logo_white.png/" }) {
        childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `);

  const colors = useColor();
  const logo = useMemo(() => {
    if (color === 'light') {
      return logoImage;
    } else {
      return logoWhiteImage;
    }
  }, [color, logoImage, logoWhiteImage]);

  return (
    <div
      css={css`
        display: flex;
        align-items: center;
        justify-content: center;
      `}
      {...rest}
    >
      <Image
        css={css`
          width: ${size}px;
          margin-right: 0.5rem;
        `}
        fluid={logo.childImageSharp.fluid}
        alt="Lubycon 로고"
      />
      <span
        css={css`
          color: ${colors.text.secondary};
          font-size: ${size / 23}rem;
          ${Media.mobile} {
            display: none;
          }
        `}
      >
        LUBYCON
      </span>
    </div>
  );
};

export default LubyconLogo;
